import "../assets/css/button.css!";
import jquery = require("jquery");
import {AVisualComponent} from "../../component/abstract/a-visual-component";
import {IButton} from "../interface/i-button";
import {IPopover} from "../../widget";
import {EBasicColorStatus} from "../../component/enum/e-basic-color-status";
import {EVerticalAlign} from "../../component/enum/e-vertical-align";
import {EHorizontalAlign} from "../../component/enum/e-horizontal-align";



/** 
 * @abstract
 * @extends {AVisualComponent}
 */
export abstract class AButton extends AVisualComponent implements IButton{
	public setDisabled(disabled: boolean): this {
		if (disabled) {
			jquery(this.element).attr("disabled", "true").addClass("Disabled").css("opacity", 0.6);
		} else {
			jquery(this.element).removeAttr("disabled").removeClass("Disabled").css("opacity", 1);
		};
		return this;
	}

	public setEnable(enable: boolean): this {
		this.setDisabled(!enable);
		return this;
	}

	public isDisabled(): boolean {
		return jquery(this.element).hasClass("Disabled");
	}

	protected getColors(): string {
		return Object.keys(EBasicColorStatus)
			.map(k => EBasicColorStatus[k])
			.filter(v => typeof v === "string")
			.map(k => 'btn-' + k.toLowerCase())
			.join(' ')+' btn-default';
	}

	public setVerticalAlign(align:EVerticalAlign):this{
		if(align===EVerticalAlign.BOTTOM){
			jquery(this.element).removeClass("btn_v_top").addClass("btn_v_bottom");
		}else{
			jquery(this.element).removeClass("btn_v_bottom").addClass("btn_v_top");
		};
		return this;
	}

	public getVerticalAlign():EVerticalAlign{
		   if(jquery(this.element).hasClass("btn_v_top")){
			   return EVerticalAlign.TOP;
		   }else if(jquery(this.element).hasClass("btn_v_bottom")){
			   return EVerticalAlign.BOTTOM;
		   }
		   return null;
	}
	
   public setHorizontalAlign(align:EHorizontalAlign):this{
		if(align===EHorizontalAlign.LEFT){
			jquery(this.element).removeClass("pull-right").addClass("pull-left");
		}else{
			jquery(this.element).removeClass("pull-left").addClass("pull-right");
		};
		return this;
	}

	abstract setLabel(label: string): this;
	abstract setTooltip(tooltip: string): this;

	public append(popover: IPopover): this{
		jquery(this.element).css('position', 'relative');
		let popoverTop:number = 36;//jquery(this.element).outerHeight() + 2;
		jquery(popover.element).find('.arrow').css('left', '11%');
		jquery(popover.element).css('top', popoverTop);
		jquery(this.element).append(jquery(popover.element));      
		return this;              
	}
}
